<template>
	<el-row>
		<el-col :span="24">
				<div class="amap-page-container">
					<el-amap-search-box class="search-box" :search-option="searchOption" :on-search-result="onSearchResult"></el-amap-search-box>
					<el-amap vid="amapDemo" :center="mapCenter" :zoom="zoom" :plugin="plugin" class="amap-demo" :events="events">
						<el-amap-marker v-for="marker in markers" key="marker" :position="marker.position"  :events="marker.events" :draggable="marker.draggable"></el-amap-marker>
						<el-amap-info-window v-for="window in windows" key="window" :position="window.position" :visible="window.visible" ></el-amap-info-window>
  					</el-amap>
				</div>
				<!-- <div class="toolbar">
			        position: [{{lng}}]address: {{ address }}
			     </div> -->
			<el-dialog
				title="" :visible.sync="dialogVisible1" size="tiny" :modal="false">
					<span>  {{ address }}</span>
				 	<span slot="footer" class="dialog-footer">
					    <el-button @click="dialogVisible1 = false"> </el-button>
					    <el-button type="primary" @click="handleClose"> </el-button>
				 	</span>
			</el-dialog>
		</el-col>
	</el-row>
</template>
<script>
	import Vue from 'vue'
	//
	//
	//
	// import VueAMap  from 'vue-amap';
	// Vue.use(VueAMap );
	// VueAMap .initAMapApiLoader({
	// 	//Key
	//   key: '1d01b9d0eb92533c11679faa12762fcc',
	//   plugin: ['AMap.Autocomplete', 'AMap.PlaceSearch', 'AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType', 'AMap.PolyEditor', 'AMap.CircleEditor',"AMap.Geocoder"]
	// });
	export default{
		name:"JCDecauxmap",
		props:["dialogVisible"],
		data(){
			let self = this;
			return{
				//
		        dialogVisible1:false,
		        //
	            zoom: 12,
	            //
	            mapCenter: [121.59996, 31.197646],
	            windows:[{
	            	//
	            	position: [121.59996, 31.197646],
	            	// content: `${self.address}`,
	            	//
	            	visible: true,
	            }],
	            markers: [{ 
	                //
	                position: [121.59996, 31.197646],
	                events: {
	                    click(e) {

		                    let { lng, lat } = e.lnglat;
		                    self.lng = lng;
		                	self.lat = lat;
			                //  SDK 
			                // 
			                var geocoder = new AMap.Geocoder({
				                radius: 1000,
				                extensions: "all"
			                });        
		                	geocoder.getAddress([lng ,lat], function(status, result) {
		                		if (status === 'complete' && result.info === 'OK') {
		                    		if (result && result.regeocode) {
		                    			self.address = result.regeocode.formattedAddress;
		                    			self.$nextTick();
		                  			}
		                		}
		              		}); 
		              		self.windows.forEach(window => {
			                  window.visible = false;
			                });
		              		self.$nextTick(() => {
		              			self.windows[0].position = [lng, lat ]
			                    self.windows[0].visible = true   
			                    self.mapCenter=[lng, lat]
	              				self.markers[0].position=[lng, lat] 
			                }); 
			                self.dialogVisible1=true	  
		            	},
                	},
	            //
	            visible: true,
	            }],
	            //
                events: {
                    click(e) {
	                    let { lng, lat } = e.lnglat;
	                    self.lng = lng;
	                	self.lat = lat;
		                //  SDK 
		                // 
		                var geocoder = new AMap.Geocoder({
			                radius: 1000,
			                extensions: "all"
		                });        
	                	geocoder.getAddress([lng ,lat], function(status, result) {
	                		if (status === 'complete' && result.info === 'OK') {
	                    		if (result && result.regeocode) {
	                    			self.address = result.regeocode.formattedAddress;
	                    			self.$nextTick();
	                  			}
	                		}
	              		}); 
	              		self.windows.forEach(window => {
		                  window.visible = false;
		                });
	              		self.$nextTick(() => {
	              			self.windows[0].position = [lng, lat ] 
	              			self.mapCenter=[lng, lat]
	              			self.markers[0].position=[lng, lat]
		                    self.windows[0].visible = true    
		                });   

	            	}
                },
	            //
	            address: '',
	            //
	            lng: 0,
          		lat: 0,
	            //
	            plugin: ['ToolBar', {
		            pName: 'MapType',
		            defaultType: 0,
		            
		          }],
	            //
	            draggable: false,
	            //
	            searchOption: {
		            city: '',
		            citylimit: true
		        },
			}
		},
		methods:{
	        //
	        onSearchResult(pois) {
	            let latSum = 0;
	            let lngSum = 0;
	            let markers = [];
        		let windows = [];
        		let self = this;
	            this.markers=[]
	            console.log(pois)
	            if (pois.length > 0) {
		            pois.forEach( (poi, index) => {
		            	console.log(index)
		                let {lng, lat} = poi;
		                lngSum += lng;
		                latSum += lat;
		                this.markers.push({
		              		position:[poi.lng, poi.lat],
		              		events: {
				                click() {
					                self.windows.forEach(window => {
					                  window.visible = false;
					                });
					                self.$nextTick(() => {
					                  self.windows[index].visible = true;
					                });
				                    self.lng = poi.lng;
				                	self.lat = poi.lat;
					                //  SDK 
					                // 
					                var geocoder = new AMap.Geocoder({
						                radius: 1000,
						                extensions: "all"
					                });        
				                	geocoder.getAddress([lng ,lat], function(status, result) {
				                		if (status === 'complete' && result.info === 'OK') {
				                    		if (result && result.regeocode) {
				                    			self.address = result.regeocode.formattedAddress;
				                    			self.$nextTick();
				                  			}
				                		}
				              		}); 
				              		self.dialogVisible1=true   
				                },
				            }
			            });
			            windows.push({
			            	position: [poi.lng, poi.lat],
				            visible: false
			          	});
		            });
		            let center = {
		                lng: lngSum / pois.length,
		                lat: latSum / pois.length
		            };
		            this.mapCenter = [center.lng, center.lat];
		            this.windows = windows
		        }
	        },
        	handleClose(){
				this.dialogVisible1 = false
				this.$emit("PopController",{
					dialogVisible:!this.dialogVisible,
					//
					lng:this.lng,
					lat:this.lat,
					address:this.address
				})
				
			}
        	
		}
	}
</script>
<style>
	
</style>


