<template>
	<el-row>
		<el-col :span="24">
			<p class="jisp"></p>	
    		<div class="div">
				<el-row>
					<el-col :span="24" class="main">
						<label></label>
						<v-distpicker hide-area></v-distpicker>
					</el-col>
				</el-row>
				<el-row>
					<el-col :span="24" class="main">
						<label></label>
						<el-button size="small" @click="dialogVisible = true"></el-button>
						<label></label>
						<input v-model="input" placeholder=""></input>
						<label></label>
						<label class="address">{{address}}</label>
						<el-dialog title="" :visible.sync="dialogVisible" size="large">
							<jcdecauxmap :dialogVisible="dialogVisible" @PopController="getdialogVisibles"/>
						</el-dialog>
					</el-col>
				</el-row>
				<el-row>
					<el-col :span="24" class="main">
						<label></label>
						<el-date-picker
					      v-model="value1"
					      type="date"
					      placeholder=""
					      :picker-options="pickerOptions0">
					    </el-date-picker>
					    <label></label>
					    <el-date-picker
					      v-model="value2"
					      type="date"
					      placeholder=""
					      :picker-options="pickerOptions0">
					    </el-date-picker>
					    <el-button type="primary" class="button"  @click=""> </el-button>
						<el-button @click=""> </el-button>
					</el-col>
				</el-row>
				<el-row>
					<el-col :span="20" class="main">
						<el-table
							border
							stripe
						    :data="consumeQueryList"
						    style="width: 100%">
						    <el-table-column
						      prop="userBaseInfo.userId"
						      label="ID"
						      width="200">
						    </el-table-column>
						    <el-table-column
						      prop="userBaseInfo.userName"
						      label=""
						      width="180">
						    </el-table-column>
						    <el-table-column
						      prop="tradeTime"
						      label=""
						      width="180%">
						    </el-table-column>
						     <el-table-column
						      prop="payOrderInfo.cityCode"
						      label=""
						      width="100">
						    </el-table-column>
						     <el-table-column
						      prop="paymentType"
						      label=""
						      width="100%">
						    </el-table-column>
						     <el-table-column
						      prop="number"
						      label=""
						      width="100%">
						    </el-table-column>
						     <el-table-column
						      prop="payOrderInfo.orderNumber"
						      label=""
						      width="210">
						    </el-table-column>
						    <el-table-column
						      prop="tradeName"
						      label=""
						      width="190%">
						    </el-table-column>
						</el-table>
					</el-col>
				</el-row>
				<el-row>
			    	<el-col :span="20" class="table">
			    		<el-pagination
					        @current-change="handleCurrentChange"
					        :current-page="currentPage"
					        :page-size="pageSize"
					        layout="prev, pager, next, jumper"
					        :total="totalElements">
					    </el-pagination>
			    	</el-col>
	    		</el-row>
	    	</div>
		</el-col>
	</el-row>
</template>
<script>
	import Vue from 'vue'
	import {mapMutations,mapActions, mapState} from "vuex"
	//
	import VDistpicker from 'v-distpicker'
	import jcdecauxmap from "./JCDecauxmap/JCDecauxmap.vue"
	//
	// import VueAMap  from 'vue-amap';
	// Vue.use(VueAMap );
	// VueAMap .initAMapApiLoader({
	// 	//Key
	//   key: '1d01b9d0eb92533c11679faa12762fcc',
	//   plugin: ['AMap.Autocomplete', 'AMap.PlaceSearch', 'AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType', 'AMap.PolyEditor', 'AMap.CircleEditor',"AMap.Geocoder"]
	// });
	// let amapManager = new AMap.AMapManager();
	export default{
		name:"consumeQuery",
		components: { 
			VDistpicker,
			jcdecauxmap
		},
		created(){
			this.CONSUMEQUERYLIST()
			this.currentPage = this.pageIndex
		},
		data(){
			return{
				province: "",
  				city: "",
						        //
		         pickerOptions0: {
		          disabledDate(time) {
		            return time.getTime() < Date.now() - 8.64e7;
		          }
		        },
		         //
		        lng:"",
				lat:"",
				address:"",
		        value1:"",
		        value2:"",
		        //
		        dialogVisible:false,
		        //
		        input:"",
				//
				currentPage:1,
			}
		},
		computed:{
	    	...mapState("consumeQuery",["pageSize","totalElements","pageIndex","consumeQueryList"])
	    },
		methods:{
			...mapMutations("consumeQuery",["getPageSize"]),
			...mapActions("consumeQuery",["CONSUMEQUERYLIST"]),
	      	 //
        	handleCurrentChange(val) {
        		this.getPageSize(val)
        		this.currentPage = this.pageIndex
        		this.CONSUMEQUERYLIST()
        	},
        	getdialogVisibles(data){
        		this.dialogVisible = data.dialogVisible,
					//
				this.lng = data.lng,
				this.lat = data.lat,
				this.address = data.address
        		console.log(this)
        	}
		}
	}
</script>
<style >
	.main{
		display: flex;
		margin-bottom: 20px;
	}
	.main >*{
		margin-right: 20px;
	}
	.amap-page-container {
      position: relative;
    }
	.amap-demo {
      height: 500px;
    }
    .search-box {
      position: absolute;
      top: 10px;
      left: 60px;
    }
    .address {
    	border-bottom: 1px solid #333;
    }
    .button{
    	margin-left: 40px;
    }
    .table{
		padding-top: 20px;
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-end;
	}
	.jisp{
		margin: 0;
		height: 70px;
		color: #353535;
		font-size: 20px;
		line-height: 70px;
		background-color: #f2f2f2;
		padding-left: 30px;
		margin-bottom: 25px;
	}
	.div{
		padding:0 30px;
	}
</style>


